import {useState} from "react";
import styled from "styled-components";
import {useNavigate} from "react-router-dom";

import SignBg from "../../assets/images/login/signBtn.png";
import btnBg from "../../assets/images/login/Btnbg.png";
import LoginBg from "../../assets/images/login/loginBtn.png";
import ClearImg from "../../assets/images/login/clear_nor.png";
import ShowImg from "../../assets/images/login/show_nor.png";
import hideImg from "../../assets/images/login/hide_nor.png";
import {getVerifyCode, forgot} from "../../api/apiHttp";
import md5 from "js-md5";
import {useWeb3} from "../../api/contracts";


const Main = styled.div`
  flex-grow: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
`
const BtnGroup = styled.div`
    display: flex;
    width: 4rem;
    justify-content: space-between;
    padding-top: 0.64rem;
`

const SignBtn = styled.div`
    width: 1.36rem;
    height: 0.56rem;
    background: url(${SignBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    cursor: pointer;
`
const LoginBtn = styled.button`
    width: 2.4rem;
    height: 0.56rem;
    background: url(${btnBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    color: rgba(13, 12, 11, 1);
    border: 0;
    font-family: 'Orbitron-Medium';
    cursor: pointer;
    &:disabled{
      background: url(${LoginBg}) no-repeat;
      color: rgba(13, 12, 11, 0.45);
      background-size: 100% 100%;
    }
    
`

const Ulbox = styled.ul`
    li{
      border-bottom: 1px solid rgba(255, 250, 242, 0.25);
      padding: 0.32rem 0 0.08rem;
      position: relative;
      .tips,.tipsSend{
        position: absolute;
        left: 0;
        bottom:-0.21rem;
        color: #EC1818;
        font-size: 0.12rem;
      }
      .tipsSend{
        color: #FF9500;
      }
    }
  .title{
    font-size: 0.14rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color: rgba(255, 250, 242, 0.7);
    line-height: 0.24rem;
    margin-bottom: 0.08rem;
  }
  .content{
    display: flex;
    justify-content: space-between;
    img{
      width: 0.24rem;
      height: 0.24rem;
      cursor: pointer;
    }
    span{
      opacity: 0.45;
    }
    .again{
      color: #FF9500;
      opacity: 1;
    }
    input{
      flex-grow: 1;
      border: 0;
      font-size: 0.14rem;
      font-family: 'orbitron-regular';
      height: 0.24rem;
      box-sizing: border-box;
      padding-top: 0.03rem;
      color: #ffffff;
      background: none;
      &:focus{
        outline: none;
      }
    }
  }
`

export default function Forget(){
    const {  dispatch } = useWeb3();
    const navigate = useNavigate();

    const [ email, setEmail ] = useState('');
    const [ type, setType ] = useState(['password','password']);
    const [ pwd, setPwd ] = useState('');
    const [ rePwd, setRePwd ] = useState('');
    const [ error, setError ] = useState(false);
    const [ captcha, setCaptcha ] = useState('');
    const [ tips, setTips ] = useState('');
    const [ showTime, setShowTime ] = useState(false);
    const [ time, setTime ] = useState(60);
    const [ captchaTips, setCaptchaTips ] = useState(false);
    const [ again, setAgain] = useState(false);

    const ClearInput = () =>{
        setEmail('')
    }
    const GoLogin = () =>{
        navigate("/loginSign/1")
    }
    const ChangeType = (index) =>{
        let arr = [...type];
        arr[index] = arr[index]==='text'?'password':'text'
        setType(arr);
    }

    const GetCaptcha = () =>{
        setAgain(true)
        getVerifyCode(email).then((dataObj) =>{
            const {data} = dataObj;
            if(data.code === 200){
                setCaptchaTips(true)
            }
        })
        let timer = time;
        setShowTime(true);
        const t = setInterval(()=>{
            timer-- ;
            if(timer<= 0){
                clearInterval(t);
                setShowTime(false);
                setCaptchaTips(false)
            }
            setTime(timer);

        },1000)
    }

    const handleinput = (e,inputType) =>{
        setError(false)
        switch(inputType){
            case 'email':
                setEmail(e.target.value);
                break;
            case 'pwd':
                setPwd(e.target.value);
                break;
            case 'captcha':
                setCaptcha(e.target.value);
                break;
            case 'rePwd':
                setError(false);
                setTips('* Your passwords did not match');
                setRePwd(e.target.value);
                break;
            default:break;
        }

    }

    const letsBegin = () =>{
        if( rePwd !== pwd){
            setError(true)
            return;
        }else{

            const dataObj = {
                account: email,
                newPassword: md5(pwd),
                verifyCode:captcha
            }
            console.log("===dataObj===",dataObj)
            dispatch({type: 'SET_LOADING',payload:true});
            forgot(dataObj).then((dataObject)=>{
                dispatch({type: 'SET_LOADING',payload:null});
                const {data} = dataObject;
                if(data.code === 200){
                    dispatch({type: 'SET_LOGIN',payload:data.data});
                    navigate('/');
                }else{
                    setError(true)
                    setTips(data.msg);
                }
            })
        }
    }
    return<Main>
            <Ulbox>
                <li>
                    <div className="title">Enter email or phone number</div>
                    <div className="content">
                        <input type="text" value={email} placeholder="E-mail or phone"  autoComplete="new-password"  onChange={(e)=>handleinput(e,'email')}/>
                        <img src={ClearImg} alt="" onClick={()=>ClearInput()}/>
                    </div>
                    {/*<div className="tips">* Here is the error message</div>*/}
                </li>
                <li>
                    <div className="title">Please enter your Captcha</div>
                    <div className="content">
                        <input type="text" placeholder="Captcha"  autoComplete="new-password" value={captcha} onChange={(e)=>handleinput(e,'captcha')}/>
                        {
                            !showTime && <span onClick={()=>GetCaptcha()} className={again?'again':''}>{again?'Again':'Get'}</span>
                        }
                        {
                            showTime && <span className="again">{time}s</span>
                        }
                    </div>
                    {
                        captchaTips &&<div className="tipsSend"> Captcha is sent</div>
                    }
                </li>
                <li>
                    <div className="title">Set your password</div>
                    <div className="content">
                        <input type={type[0]} placeholder="Please enter your password"  autoComplete="new-password" value={pwd} onChange={(e)=>handleinput(e,'pwd')} />
                        {
                            type[0] ==='password' && <img src={ShowImg} alt="" onClick={()=>ChangeType(0)}/>
                        }
                        {
                            type[0] ==='text' && <img src={hideImg} alt="" onClick={()=>ChangeType(0)}/>
                        }
                    </div>
                    {/*<div className="tips">* Here is the error message</div>*/}
                </li>
                <li>
                    <div className="title">Confirm your password</div>
                    <div className="content">
                        <input  type={type[1]} placeholder="Re-confirm your password" autoComplete="new-password" value={rePwd}  onChange={(e)=>handleinput(e,'rePwd')}/>
                        {
                            type[1] ==='password' && <img src={ShowImg} alt="" onClick={()=>ChangeType(1)}/>
                        }
                        {
                            type[1] ==='text' && <img src={hideImg} alt="" onClick={()=>ChangeType(1)}/>
                        }
                    </div>
                    {
                        error && <div className="tips">{tips}</div>
                    }
                </li>
            </Ulbox>
            <BtnGroup>
                <SignBtn onClick={()=>GoLogin()}>Log in</SignBtn>
                <LoginBtn  disabled={ !pwd.length || !rePwd.length || !email.length || !captcha.length}  onClick={()=>letsBegin()}>Let's begin</LoginBtn>
            </BtnGroup>
        </Main>
}