export default function Step1(props){

    const navigate = useNavigate();
    const [ user, setUser ] = useState('');
    const [ email, setEmail ] = useState('');
    const [ captcha, setCaptcha ] = useState('');
    const [ userShow, setUserShow ] = useState(false);
    const [ emailShow, setEmailShow ] = useState(false);
    const [ captchaShow, setCaptchaShow ] = useState(false);
    const [ showTime, setShowTime ] = useState(false);
    const [ time, setTime ] = useState(60);
    const [ captchaTips, setCaptchaTips ] = useState(false);
    const [ again, setAgain] = useState(false);

    const ClearInput = (type) =>{
        if(type==='user'){
            setUser('');
        }else{
            setEmail('');
        }

    }
    const GoLogin = () =>{
        navigate("/loginSign/1")
    }
    const handleinput = (e,inputType) =>{
        switch(inputType){
            case 'user':
                setUser(e.target.value);
                setUserShow(!user.length);
                break;
            case 'email':
                setEmail(e.target.value);
                setEmailShow(!email.length);
                break;
            case 'captcha':
                setCaptcha(e.target.value);
                setCaptchaShow(!email.length);
                break;
            default:break;
        }

    }
    const GetCaptcha = () =>{
        setAgain(true)
        getVerifyCode(email).then((dataObj) =>{
            const {data} = dataObj;
            if(data.code === 200){
                setCaptchaTips(true)
            }
        })
        let timer = time;
        setShowTime(true);
        const t = setInterval(()=>{
            timer-- ;
            if(timer<= 0){
                clearInterval(t);
                setShowTime(false);
                setCaptchaTips(false)
            }
            setTime(timer);

        },1000)
    }

    const nextStep = () => {

        const obj= {
            user,
            email,
            captcha
        }
        props.changeShow(2)
        sessionStorage.setItem('step1',JSON.stringify(obj))
    }

    return <div>
        <AvatarBox>
            <div className="brdr">
                <Avatar bgimg={defaultAvatar} />
            </div>

            {/*<UploadBtn>Upload avatar</UploadBtn>*/}
        </AvatarBox>
        <Ulbox>
            <li>
                <div className="title">user name</div>
                <div className="content">
                    <input type="text" value={user} placeholder="Please enter your in-game name"  autoComplete="new-password" onChange={(e)=>handleinput(e,'user')}/>
                    <img src={ClearImg} alt="" onClick={()=>ClearInput('user')}/>
                </div>
                {
                    userShow &&<div className="tips">* Phone number or Email Address is required</div>
                }
            </li>
            <li>
                <div className="title">Enter email or phone number</div>
                <div className="content">
                    <input type="text" placeholder="E-mail or phone"  autoComplete="new-password" value={email} onChange={(e)=>handleinput(e,'email')}/>
                    <img src={ClearImg} alt="" onClick={()=>ClearInput('email')}/>
                </div>
                {
                    emailShow &&<div className="tips">* Email or phone is required</div>
                }
            </li>
            <li>
                <div className="title">Please enter your Captcha</div>
                <div className="content">
                    <input type="text" placeholder="Captcha"  autoComplete="new-password" value={captcha} onChange={(e)=>handleinput(e,'captcha')}/>
                    {
                        !showTime && <span onClick={()=>GetCaptcha()} className={again?'again':''}>{again?'Again':'Get'}</span>
                    }
                    {
                        showTime && <span className="again">{time}</span>
                    }
                </div>
                {
                    captchaShow &&<div className="tips">* Captcha is required</div>
                }
                {
                    captchaTips &&<div className="tipsSend"> Captcha is sent</div>
                }
            </li>

        </Ulbox>
        <BtnGroup>
            <SignBtn onClick={()=>GoLogin()}>Log in</SignBtn>
            <LoginBtn disabled={ !user.length || !email.length || !captcha.length} onClick={()=>nextStep()}>Next step</LoginBtn>
        </BtnGroup>
    </div>
}