import { ethers, BigNumber } from 'ethers';

const getUserOrderIds = async (contractInstance,account,startIndex,len) =>{
    const result = await contractInstance.getUserOrderIds(account,startIndex,len);
    return  result;
}

const numberOf = async (contractInstance,account) =>{
    const result = await contractInstance.numberOf(account);
    return  result;
}

const orderList = async (contractInstance,id) =>{
    const result = await contractInstance.orderList(id);
    return  result;
}

const sellcraft = async (web3Instance,contractInstance,nft,tokenId,currency,price,NFTContract) =>{
    const signer = web3Instance.getSigner()

    const { market } = window.GeneralConfig;

    const rt = await NFTContract.connect(signer).setApprovalForAll(market,true);
    rt.wait();

    const result = await contractInstance.connect(signer).sell(nft,tokenId,currency,ethers.utils.parseEther(price.toString()));

    const receipt =  await result.wait();
    return  receipt;

}

const delay = ms => new Promise(resolve => setTimeout(resolve, ms));

const buyAircraft = async (web3Instance,contractInstance,id,NFTContract,IISContract,price) =>{

    const signer = web3Instance.getSigner()

    const { market } = window.GeneralConfig;

    const rt = await NFTContract.connect(signer).setApprovalForAll(market,true);
    rt.wait();

    await delay(5000);

    const rt2 = await IISContract.connect(signer).approve(market, price);
    rt2.wait();

    await delay(5000);

    const result = await contractInstance.connect(signer).buy(id);
    return  result.wait();

}
const cancelAircraft = async (web3Instance,contractInstance,id) =>{
    const signer = web3Instance.getSigner()

    const result = await contractInstance.connect(signer).cancel(BigNumber.from(id));
    return  result.wait();
}

const orderListLength = async (contractInstance) =>{

    const result = await contractInstance.orderListLength();
    return  result
}


export default {
    getUserOrderIds,
    numberOf,
    sellcraft,
    buyAircraft,
    cancelAircraft,
    orderList,
    orderListLength,
};
