import styled from "styled-components";
import Lftbg from "./assets/images/login/bg.png";
import Back from './assets/images/login/back_nor.png';
import {useNavigate, useParams} from "react-router-dom";
import {useEffect, useState} from "react";

import Login from "./components/loginSign/login";
import Forget from "./components/loginSign/forget";
import Register from "./components/loginSign/register";

import LoginBg from './assets/images/mobile/login/bg.png';
import LoadingModal from "./components/loadingModal";

const LoginBox = styled.div`
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-between;
  @media (max-width: 1000px) {
    background: url(${LoginBg}) no-repeat;
    background-size: 100% 100%;
  }
`
const Lft = styled.div`
    background: url(${Lftbg}) no-repeat center center;
    background-size: cover;
    width: 8.08rem;
    height: 100vh;
  @media (max-width: 1000px) {
    display: none;
  }
`

const Rht = styled.div`
    padding: 0.64rem 1rem;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  @media (max-width: 1000px) {
    padding:  0;
  }
`
const Title = styled.div`
  border-bottom: 1px solid rgba(255, 250, 242, 0.15);
  padding-bottom: 0.16rem;
  width: 4rem;
    img{
      width: 0.08rem;
      height: 0.13rem;
      margin-right: 0.08rem;
    }
  img,span{
    cursor: pointer;
  }
  @media (max-width: 1000px) {
    display: none;
  }
`
const Main = styled.div`
  flex-grow: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
`
export default function LoginSign(){

    const [type,setType] = useState(0);
    const navigate = useNavigate();
    const params = useParams();
    useEffect(()=>{
        setType(parseInt(params.type))
    },[params])

    const BackNav = () =>{
        navigate("/")
    }
    return<div>
        <LoadingModal  />
        <LoginBox>
            <Lft />
            <Rht>
                <Title>
                    <div onClick={()=>BackNav()}>
                        <img src={Back} alt=""/>
                        <span>Home</span>
                    </div>
                </Title>
                <Main>
                    {
                        type === 1 && <Login />
                    }
                    {
                        type === 2 && <Forget />
                    }
                    {
                        type === 3 && <Register />
                    }
                </Main>
            </Rht>
        </LoginBox>
    </div>
}