import axios from 'axios';

const IPFS_BASES = [
    'https://gateway.pinata.cloud/ipfs',
    'https://10.via0.com/ipfs',
    'https://ipfs.io/ipfs',
    'https://ipfs.leiyun.org/ipfs',
    'https://cloudflare-ipfs.com/ipfs',
  ];

async function agumentedIpfsGet(hash) {
    const promises = IPFS_BASES.map(ipfsBase => axios.get(`${ipfsBase}/${hash}`));

    // if (Promise.any) {
    //     return await Promise.any(promises);
    // } else {
    //     console.warn('No Promise.any, fallback to p-any');
    //     return await pAny(promises);
    // }

    return await Promise.any(promises);
}

async function getServiceProviderInfo(uri) {
    // const hash = uri.replace('/ipfs/', '');
    const resp = await agumentedIpfsGet(uri);
    return resp.data;
}

function getImage(img) {
    const hash = img.split('ipfs://')[1];
    return `${IPFS_BASES[4]}/${hash}`
}

const PublicObj = {
    getServiceProviderInfo,
    getImage
}

export default PublicObj;
