export default function MarketFoot() {
    const { dispatch, state } = useWeb3();
    const { showpage } = state;

    const [current,setCurrent] = useState('');

    const pageTotalNum = () =>{
        const { pagesize, total } = showpage;
        let lastpage = (total % pagesize) ? 1 :0;
        return parseInt(total/pagesize) + lastpage;

    }
    const toPage = (index) =>{
        const { pagesize, total} = showpage;
        dispatch({ type: 'SHOW_PAGE', payload: {page:index,pagesize,total} });
    }

    const handleChange = (e) =>{
        setCurrent(e.target.value)
    }

    const goPage = () =>{
        const { pagesize, total} = showpage;
        if(current > pageTotalNum() || current -1 <0) return;
        let curr = current;
        dispatch({ type: 'SHOW_PAGE', payload: {page:curr-1,pagesize,total} });
        setCurrent('')
    }

    return <Foot>
        <LftAll>

            <Links>
                <a href="https://t.me/iFighterInfinity" target="_blank" rel="noopener noreferrer">
                    <img src={tg} alt="" className="pc"/>
                    <img src={tgM} alt="" className="mobile"/>
                </a>
                <a href="https://twitter.com/iFighterGame" target="_blank" rel="noopener noreferrer">
                    <img src={twitter} alt=""  className="pc"/>
                    <img src={twitterM} alt="" className="mobile" />
                </a>
                <a href="https://www.youtube.com/channel/UCKmChoqJCpqTi9J0wh9U5-Q" target="_blank" rel="noopener noreferrer">
                    <img src={youtube} alt=""  className="pc"/>
                    <img src={youtubeM} alt="" className="mobile" />
                </a>
                <a href="https://www.facebook.com/IFighter-Infinity-110157721501891/?ref=pages_you_manage" target="_blank" rel="noopener noreferrer">
                    <img src={facebook} alt=""  className="pc"/>
                    <img src={facebookM} alt=""  className="mobile"/>
                </a>
                <a href="https://discord.gg/2xHzE62SQF" target="_blank" rel="noopener noreferrer">
                    <img src={discover} alt=""   className="pc"/>
                    <img src={discoverM} alt=""   className="mobile"/>
                </a>
                <a href="https://medium.com/@ifighterinfinity" target="_blank" rel="noopener noreferrer">
                    <img src={media} alt=""  className="pc"/>
                    <img src={mediaM} alt=""  className="mobile"/>
                </a>
            </Links>
        </LftAll>
        {
            showpage !=null && !! showpage.total && <Rht>
                {
                    [...Array(pageTotalNum())].map((item,index)=>(<span key={`page_${index}`} className={ showpage.page === index ? 'active' : '' } onClick={()=>toPage(index)}>{index > 10 ? index+1 : `0${index+1}`}</span>))
                }


                {/*<a href="#!"> 02</a>*/}
                {/*<a href="#!"> 03</a>*/}
                {/*<a href="#!"> 04</a>*/}
                {/*<a href="#!"> 05</a>*/}
                {/*<img src={ellips} alt=""/>*/}
                {/*<a href="#!"> 99</a>*/}
                <PageRht>
                    <input type="text" placeholder="Page" value={current} onChange={(e)=>handleChange(e)}/>
                    <button onClick={() => goPage()}>Go</button>
                </PageRht>
            </Rht>
        }

    </Foot>
}
