import { ethers } from "ethers";
import abi from "../abi/IISERC20";

let loadMain = false;
let IISContract;

const { iisToken } = window.GeneralConfig;

export default async function IISConnect(state, dispatch) {

    const {web3InstanceState, IISContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const LoadIRon = () => {

        try {
            IISContract = new ethers.Contract(iisToken, abi, web3Instance);
            dispatch({type: 'SET_IIS', payload: IISContract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'IIS_ERROR'});
        }
    };
    if (IISContractState !== 'LOAD_IIS') return;
    if (loadMain) return dispatch({type: 'SET_IIS', payload: IISContract});
    loadMain = true;
    LoadIRon();
}
