import {useState} from "react";
import {useNavigate} from "react-router-dom";
import styled from "styled-components";

import SignBg from "../../assets/images/login/signBtn.png";
import btnBg from "../../assets/images/login/Btnbg.png";
import LoginBg from "../../assets/images/login/loginBtn.png";
import ShowImg from "../../assets/images/login/show_nor.png";
import hideImg from "../../assets/images/login/hide_nor.png";
import md5 from "js-md5";
import { register } from '../../api/apiHttp';
import {useWeb3} from "../../api/contracts";

const BtnGroup = styled.div`
    display: flex;
    width: 4rem;
    justify-content: space-between;
    padding-top: 0.64rem;
`

const SignBtn = styled.div`
    width: 1.36rem;
    height: 0.56rem;
    background: url(${SignBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    cursor: pointer;
`
const LoginBtn = styled.button`
    width: 2.4rem;
    height: 0.56rem;
    background: url(${btnBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    color: rgba(13, 12, 11, 1);
    border: 0;
    font-family: 'Orbitron-Medium';
    cursor: pointer;
    &:disabled{
      background: url(${LoginBg}) no-repeat;
      color: rgba(13, 12, 11, 0.45);
      background-size: 100% 100%;
    }
    
`

const Ulbox = styled.ul`
    li{
      border-bottom: 1px solid rgba(255, 250, 242, 0.25);
      padding: 0.32rem 0 0.08rem;
      position: relative;
      .tips{
        position: absolute;
        left: 0;
        bottom:-0.21rem;
        color: #EC1818;
        font-size: 0.12rem;
      }
    }
  .title{
    font-size: 0.14rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color: rgba(255, 250, 242, 0.7);
    line-height: 0.24rem;
    margin-bottom: 0.08rem;
  }
  .orange{
    border-bottom: 1px solid #FF9500;
    .title{
      color: rgba(255, 149, 0, 1);
    }
  }
  .content{
    display: flex;
    justify-content: space-between;
    img{
      width: 0.24rem;
      height: 0.24rem;
      cursor: pointer;
    }
    input{
      flex-grow: 1;
      border: 0;
      font-size: 0.14rem;
      font-family: 'orbitron-regular';
      height: 0.24rem;
      box-sizing: border-box;
      padding-top: 0.03rem;
      color: #ffffff;
      background: none;
      &:focus{
        outline: none;
      }
    }
    span{
      opacity: 0.45;
      cursor: pointer;
    }
  }
  .userId{
    font-size: 0.16rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color: #FF9500;
    line-height: 0.28rem;
  }
`



export default function Step1(){
    const {  dispatch } = useWeb3();
    const navigate = useNavigate();

    const [ pwd, setPwd ] = useState('');
    const [ rePwd, setRePwd ] = useState('');
    const [ type, setType ] = useState(['password','password']);
    const [ error, setError ] = useState(false);
    const [ tips, setTips ] = useState('');
    
    const GoLogin = () =>{
        navigate("/loginSign/1")
    }
    const ChangeType = (index) =>{
        let arr = [...type];
        arr[index] = arr[index]==='text'?'password':'text'
        setType(arr);
    }
    const handleinput = (e,inputType) =>{
        setError(false)
        switch(inputType){
            case 'pwd':
                setPwd(e.target.value);
                break;
            case 'rePwd':
                setError(false);
                setTips('* Your passwords did not match');
                setRePwd(e.target.value);
                break;
            default:break;
        }

    }

    const beginRegister = () =>{
        if( rePwd !== pwd){
            setError(true)
            return;
        }else{
            const firstObj = JSON.parse(sessionStorage.getItem('step1'));
            const { user, email, captcha } = firstObj;

            const dataObj = {
                name: user,
                account: email,
                password: md5(pwd),
                verifyCode:captcha,
                extData: null
            }
            dispatch({type: 'SET_LOADING',payload:true});
            register(dataObj).then((dataObject)=>{
                dispatch({type: 'SET_LOADING',payload:null});
                const {data} = dataObject;
                if(data.code === 200){
                    dispatch({type: 'SET_LOGIN',payload:data.data});
                    navigate('/loginSign/1');
                }else{
                    setError(true)
                    setTips(data.msg);
                }
            })
        }
    }

    return <div>

        <Ulbox>
            {/*<li className="orange">*/}
            {/*    <div className="title">Current user ID</div>*/}
            {/*    <div className="content">*/}
            {/*      <div className="userId">123456789abc</div>*/}
            {/*    </div>*/}
            {/*</li>*/}
            <li>
                <div className="title">Set your password</div>
                <div className="content">
                    <input type={type[0]} value={pwd} placeholder="Please enter your password"  onChange={(e)=>handleinput(e,'pwd')}  autoComplete="new-password"/>
                    {
                        type[0] ==='password' && <img src={ShowImg} alt="" onClick={()=>ChangeType(0)}/>
                    }
                    {
                        type[0] ==='text' && <img src={hideImg} alt="" onClick={()=>ChangeType(0)}/>
                    }
                </div>
                {/*<div className="tips">* Here is the error message</div>*/}
            </li>
            <li>
                <div className="title">Confirm your password</div>
                <div className="content">
                    <input type={type[1]} placeholder="Re-confirm your password"  autoComplete="new-password" value={rePwd}  onChange={(e)=>handleinput(e,'rePwd')}/>
                    {
                        type[1] ==='password' && <img src={ShowImg} alt="" onClick={()=>ChangeType(1)}/>
                    }
                    {
                        type[1] ==='text' && <img src={hideImg} alt="" onClick={()=>ChangeType(1)}/>
                    }
                    {
                        error && <div className="tips">{tips}</div>
                    }

                </div>
            </li>
            {/*<li>*/}
            {/*    <div className="title">Please fill in your Token</div>*/}
            {/*    <div className="content">*/}
            {/*        <input type={type[0]} placeholder="Token"  autoComplete="new-password"/>*/}
            {/*        <img src={ClearImg} alt="" onClick={()=>ChangeType()}/>*/}
            {/*    </div>*/}
            {/*    /!*<div className="tips">* Here is the error message</div>*!/*/}
            {/*</li>*/}

        </Ulbox>
        <BtnGroup>
            <SignBtn onClick={()=>GoLogin()}>Log in</SignBtn>
            <LoginBtn disabled={!pwd.length || !rePwd.length || error} onClick={()=>beginRegister()}>Let's begin</LoginBtn>
        </BtnGroup>
    </div>
}