export default function ConfirmModal(props) {
    const {  dispatch, state } = useWeb3();
    const { showModalAct ,MarketContract,web3Instance,Mock21Contract,Mock24Contract,Mock27Contract,Mock29Contract,Account,IISContract} = state;
    const [priceNum, setPriceNum] = useState('');

    const { item,tips,buttonTips } = props;

    const handleclose = () =>{
        dispatch({type: 'SHOW_MODAL',payload:null});
    }

    const operation = async() =>{
        if(buttonTips==="upload"){
            if(!priceNum.length) return;
            dispatch({type: 'SET_LOADING',payload:true});
            console.log("===buttonTips====",buttonTips,item)
            let currency = window.GeneralConfig.iisToken;
            let NFTContract;
            switch (item.type) {
                case 21:
                    NFTContract = Mock21Contract;
                    break;
                case 24:
                    NFTContract = Mock24Contract;
                    break;
                case 27:
                    NFTContract = Mock27Contract;
                    break;
                case 29:
                    NFTContract = Mock29Contract;
                    break;
                default:break;
            }

            await marketCt.sellcraft(web3Instance,MarketContract,item.nft,item.tokenId,currency,priceNum,NFTContract,Account).then(data=>{
                dispatch({type: 'SET_LOADING',payload:null});
                dispatch({type: 'SHOW_MODAL',payload:null});
            window.location.reload()
            });
        }else if(buttonTips==="cancel"){
            dispatch({type: 'SET_LOADING',payload:true});

            await marketCt.cancelAircraft(web3Instance,MarketContract,item.orderid).then(()=>{
                dispatch({type: 'SET_LOADING',payload:null});
                dispatch({type: 'SHOW_MODAL',payload:null});
                window.location.reload()
            }).catch(error=>{
                dispatch({type: 'SET_LOADING',payload:null});
                dispatch({type: 'SHOW_MODAL',payload:null});
                dispatch({type: 'SET_ERROR',payload:{msg:error && error.data&&error.data.message ?error.data.message  :error.message} });
            });
        }else if(buttonTips==="buy"){
            dispatch({type: 'SET_LOADING',payload:true});
            const { mockX21,mockX24,mockX27,mockX29} = window.GeneralConfig;
            let NFTContract;
            switch (item.nft) {
                case mockX21:
                    NFTContract = Mock21Contract;
                    break;
                case mockX24:
                    NFTContract = Mock24Contract;
                    break;
                case mockX27:
                    NFTContract = Mock27Contract;
                    break;
                case mockX29:
                    NFTContract = Mock29Contract;
                    break;
                default:break;
            }

            console.error("=====currency=====",item.currency)

            await marketCt.buyAircraft(web3Instance,MarketContract,item.orderid,NFTContract,IISContract,item.price).then(()=>{
                dispatch({type: 'SET_LOADING',payload:null});
                dispatch({type: 'SHOW_MODAL',payload:null});
                window.location.reload()
            }).catch(error=>{
                dispatch({type: 'SET_LOADING',payload:null});
                dispatch({type: 'SHOW_MODAL',payload:null});
                dispatch({type: 'SET_ERROR',payload:{msg:error && error.data&&error.data.message ?error.data.message  :error.message} });
            });
        }
    }

    const handleChange = (e) =>{
        setPriceNum(e.target.value)
    }



    return   <Modal show={showModalAct}  noFoot={true}>
        {
            item != null &&  <MainContent>
                <Imgs src={PublicObj.getImage(item.info.image)} alt=""/>
                <Tips>{tips}</Tips>
                <Line src={listli} alt=""/>
                <Row>
                    <SubTit>Warship Name:</SubTit>
                    <Speed>{item.info.name}</Speed>
                </Row>
                {
                    buttonTips === 'upload'  && <Row>
                        <SubTit>Warship Price:</SubTit>
                        <Speed><img src={price} alt=""/>
                            <InputBg type="number" value={priceNum} onChange={(e)=>handleChange(e)}/>
                        </Speed>
                    </Row>
                }
                {
                    buttonTips !== 'upload' && item.price && <Row>
                        <SubTit>Warship Price:</SubTit>
                        <Speed><img src={price} alt=""/>{ethers.utils.formatEther(item.price.toString())} <span>{item.info.symbol}</span></Speed>
                    </Row>
                }


                <ButtonGroup>
                    <BtnBuy onClick={()=>operation()}>{buttonTips}</BtnBuy>
                    <BtnCancel onClick={()=>handleclose()}>{buttonTips !=='cancel'?'Cancel' :'Close'}</BtnCancel>
                </ButtonGroup>
            </MainContent>
        }


    </Modal>
}
