import {useState} from "react";
import styled from "styled-components";
import NavBg from "../../assets/images/nav_hover.png";
import LogInner from "./logInner";
import Captcha from "./Captcha";


const Box = styled.div`
    min-height: 4.24rem;
`
const First = styled.div`
  display: flex;
  font-family: 'orbitron-regular';
  color: #FFFAF2;
  font-size: 0.16rem;
  line-height: 0.28rem;
  &>div{
    opacity: 0.45;
    margin-right: 0.48rem;
    padding-bottom: 0.24rem;
    cursor: pointer;
    margin-top: 3px;
  }
  .active{
    opacity: 1;
    margin-top: 0px;
    font-family: 'Orbitron-Medium';
    color: #FF9500;
    background: url(${NavBg}) no-repeat left bottom;
  }
`

export default function Login(){

    const [current, setCurrent] = useState(1);

    const handleClick = (index) =>{
        setCurrent(index)
    }

    return <Box>
        <First>
            <div className={ current === 1 ? "active" : ""} onClick={()=>handleClick(1)}>Password Login</div>
            <div className={ current === 2 ? "active" : ""} onClick={()=>handleClick(2)}>Captcha Login</div>
        </First>
        <div>
            {
                current === 1 && <LogInner />
            }
            {
                current === 2 && <Captcha />
            }

        </div>
    </Box>
}