export default function Aircraft() {

    const {dispatch,state} = useWeb3();
    const { Mock29Contract,Mock21Contract,Mock24Contract,Mock27Contract,MarketContract,showpage} = state;
    const [activeItem, setActiveItem] = useState(null);
    const [page,setPage] = useState(0);
    const [pagesize,setPagesize] = useState(1);
    const [pageTotal, setPageTotal] = useState(0);
    const [arrlist, setArrlist] = useState([]);
    const [dList, setDList] = useState([]);

    useEffect(() => {

        if(document.body.clientWidth > 1000){
            setPagesize(4)
        }else{
            setPagesize(2)
        }
    },[]);
    useEffect(() => {
        if(showpage == null ) return;
        if(showpage.page === page || showpage.page  <0) return;
        setPage(parseInt(showpage.page))
    },[showpage]);

    useEffect(() => {
        // dispatch({ type: 'SHOW_PAGE', payload: true });
        if(!arrlist.length) return;

        dispatch({ type: 'SHOW_PAGE', payload: {page,pagesize,total:arrlist.length} });
        getDisplay()

    },[arrlist,page,pageTotal]);


    useEffect(() => {
        // dispatch({ type: 'SHOW_PAGE', payload: true });
        if(MarketContract == null || Mock21Contract ==null || Mock24Contract ==null|| Mock27Contract ==null|| Mock29Contract ==null) return;
        init();

    },[MarketContract,Mock29Contract,Mock21Contract,Mock24Contract,Mock27Contract]);

    const init = async()=>{
        dispatch({type: 'SET_LOADING',payload:true});
        let len  = await marketCt.orderListLength(MarketContract);
        let arr = [];
        for(let i = 0 ;i<len;i++){
            let obj =  await marketCt.orderList(MarketContract,i.toString());
            const { mockX21,mockX24,mockX27,mockX29} = window.GeneralConfig;

            if(!obj.status)return;
            let contract
            switch (obj.nft) {
                case mockX21:
                    contract = Mock21Contract;
                    break;
                case mockX24:
                    contract = Mock24Contract;
                    break;
                case mockX27:
                    contract = Mock27Contract;
                    break;
                case mockX29:
                    contract = Mock29Contract;
                    break;
                default:continue;
            }
            let tokenURI = await mockx.tokenURI(contract,obj.tokenId);
            let tokenAddr = tokenURI.split("ipfs://")[1];

            let tokenObj = await PublicObj.getServiceProviderInfo(tokenAddr);


            if(obj.status === 1) {
                arr.push({
                    seller:obj.seller,
                    nft:obj.nft,
                    currency:obj.currency,
                    tokenId:obj.tokenId.toString(),
                    price:obj.price.toString(),
                    timestamp:obj.timestamp.toString(),
                    status:obj.status,
                    info:tokenObj,
                    orderid: i
                })
            }

        }
        let arrObj = FormatJson(arr);
        setArrlist(arrObj)
        dispatch({type: 'SET_LOADING',payload:null});

    }
    const FormatJson = (arrObj) =>{
        if(!arrObj)return;
        arrObj.map(item=>{
            let attr={}
            item.info.attributes.map(p=> attr[p.trait_type]=p.value)
            item.attr = attr;
            return item;
        })
        return arrObj

    }
    const getDisplay = () =>{
        if(arrlist.length < pagesize){
            setDList(arrlist)
        }else{
            let pageTotal =  arrlist.length / pagesize;
            setPageTotal(pageTotal)
            if(page > pageTotal || page <0) {
                return;
            }else{
                let startIndex = page * pagesize;
                let endIndex = (page + 1) * pagesize
                let currentArr = arrlist.slice(startIndex,endIndex);
                setDList(currentArr);
            }
        }
    }


    const buyAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
    }

    return <Box>
        <ConfirmModal item={activeItem} tips="Please confirm if you need to purchase this aircraft ."  buttonTips={'buy'}/>
        <Ul>
            {
                dList && dList.map((item,index)=>(<li key={`aircraft_${index}`}>
                    <Imgs src={PublicObj.getImage(item.info.image)} alt=""/>
                    <Titles>{item.info.name}</Titles>
                    <div>
                        <ImgRht src={listli} alt="" />
                        <ImgRht src={Line2} alt="" />
                    </div>

                    <SubTit>Energy recovery speed</SubTit>
                    <Speed><span>{item.attr.Recover_Speed}</span></Speed>
                    <SubTit>Number of equipment</SubTit>
                    <Speed><span>{item.attr.Slots}</span></Speed>
                    <SubTit>Owner</SubTit>
                    <Speed><span>{item.seller}</span></Speed>
                    <LastRow>
                        {
                            item.price &&<LftPrice>
                                <img src={PriceImg} alt=""/>
                                <Price>{ethers.utils.formatEther(item.price.toString())} <span>{item.symbol}</span></Price>
                            </LftPrice>
                        }

                    </LastRow>
                    <RhtA onClick={()=>buyAircraft(item)} >buy</RhtA>
                    {/*<RhtA onClick={()=>buyAircraft(item)} disabled={ethers.utils.getAddress(item.seller) === ethers.utils.getAddress(Account)}>buy</RhtA>*/}
                </li>))
            }
        </Ul>
        </Box>

}
