import {useState} from "react";
import styled from "styled-components";

import Step1 from "./step1";
import Step2 from './step2'

const Main = styled.div`
  flex-grow: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
`

const Step = styled.div`
    text-align: right;
  font-size: 0.14rem;
  font-family: 'orbitron-regular';
  font-weight: 400;
  color: rgba(255, 250, 242, 0.45);
  line-height: 0.24rem;
  span{
    cursor: pointer;
  }
  .active{
    color: rgba(255, 149, 0, 1);
    font-size: 0.2rem;
    font-family: 'orbitron';
    font-weight: bold;
    color: #FF9500;
    line-height: 0.28rem;
  }
`


export default function Register(){

    const [current,setCurrent] = useState(1)
    const changeShow = (i) =>{
        let data = sessionStorage.getItem('step1')
        if(data){
            setCurrent(i)
        }

    }

    return<Main>
        <Step>
            <span className={current === 1 ?"active" :""} onClick={()=>changeShow(1)}>01</span> / <span className={current === 2 ?"active" :""} onClick={()=>changeShow(2)}>02</span>
        </Step>
        {
            current === 1 && <Step1 changeShow={changeShow}/>
        }
        {
            current === 2 && <Step2 />
        }

    </Main>

}