import {useState} from "react";
import {useNavigate} from "react-router-dom";
import styled from "styled-components";
import md5 from 'js-md5';

import InputBg from "../../assets/images/login/inputbg.png";
import userImg from "../../assets/images/login/user_nor.png";
import ClearImg from "../../assets/images/login/clear_nor.png";
import PasswordImg from  "../../assets/images/login/password_nor.png";
import ShowImg from "../../assets/images/login/show_nor.png";
import SignBg from "../../assets/images/login/signBtn.png";
import LoginBg from "../../assets/images/login/loginBtn.png";
import btnBg from "../../assets/images/login/Btnbg.png";
import hideImg from "../../assets/images/login/hide_nor.png";

import {getLogin } from '../../api/apiHttp';
import {useWeb3} from "../../api/contracts";


const Box = styled.div`
    margin-top: 0.48rem;
`

const InputBox = styled.div`
    background: url(${InputBg}) no-repeat;
    background-size: 100% 100%;
    display: flex;
    padding: 0.18rem;
    align-content: center;
    width: 4rem;
    height: 0.56rem;
    margin-bottom: 0.32rem;
    box-sizing: border-box;
    position: relative;
    img{
        width: 0.24rem;
        height: 0.24rem;
        &:last-child{
          cursor: pointer;
        }
    }
    input{
        flex-grow: 1;
        border: 0;
        font-size: 0.14rem;
        font-family: 'orbitron-regular';
        height: 0.24rem;
        box-sizing: border-box;
        padding-top: 0.03rem;
        color: #ffffff;
        background: none;
        margin-left: 0.08rem;
      &:focus{
        outline: none;
      }
    }
  .tips{
    position: absolute;
    left: 0.16rem;
    bottom:-0.21rem;
    color: #EC1818;
    font-size: 0.12rem;
  }
`

const BtnGroup = styled.div`
    display: flex;
    width: 4rem;
    justify-content: space-between;
    padding-top: 0.32rem;
`

const SignBtn = styled.div`
    width: 1.36rem;
    height: 0.56rem;
    background: url(${SignBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    cursor: pointer;
`
const LoginBtn = styled.button`
    width: 2.4rem;
    height: 0.56rem;
    background: url(${btnBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    color: rgba(13, 12, 11, 1);
    border: 0;
    font-family: 'Orbitron-Medium';
    cursor: pointer;
    &:disabled{
      background: url(${LoginBg}) no-repeat;
      color: rgba(13, 12, 11, 0.45);
      background-size: 100% 100%;
    }
    
`

const ForgetBox = styled.div`
    width: 4rem;
    text-align: right;
    margin-top: 0.16rem;
    font-size: 0.12rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color: #FF9500;
    line-height: 0.2rem;
    cursor: pointer;
`

export default function LogInner(){
    const navigate = useNavigate();
    const {  dispatch } = useWeb3();

    const [ type, setType ] = useState('password');
    const [ user, setUser ] = useState('');
    const [ pwd, setPwd ] = useState('');
    const [ userShow, setUserShow ] = useState(false);
    const [ pwdShow, setPwdShow ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ tips, setTips ] = useState('');

    const ChangeType = () =>{
        setType(type==='text'?'password':'text');
    }

    const ClearInput = () =>{
        setUser('')
    }
    const handleinput = (e,inputType) =>{
        setError(false)
        if(inputType==='user'){
            setUser(e.target.value)
            setUserShow(!user.length)
        }else{
            setPwd(e.target.value)
            setPwdShow(!pwd.length)
        }
    }
    const TurnForget = () =>{
        navigate('/loginSign/2')
    }
    const TurnSign = () =>{
        navigate('/loginSign/3')
    }

    const login = () => {
        const pwdMD5 = md5(pwd);
        let obj = {
            account: user,
            password: pwdMD5,
            verifyCode: null
        }
        dispatch({type: 'SET_LOADING',payload:true});
        getLogin(obj).then((dataobj)=>{
            const {data} = dataobj;
            dispatch({type: 'SET_LOADING',payload:null});
            if(data.code === 200){
                dispatch({type: 'SET_LOGIN',payload:data.data});
                navigate('/')
            }else{
                setError(true)
                setTips(data.msg);
            }
        })
    }

    return <Box>
        <div>
            <InputBox>
                <img src={userImg} alt=""/>
                <input type="text" value={user} onChange={(e)=>handleinput(e,'user')} placeholder="Phone number or Email Address" autoComplete="new-password"/>
                <img src={ClearImg} alt="" onClick={()=>ClearInput()}/>
                {
                    userShow &&  <div className="tips">* Phone number or Email Address is required</div>
                }

            </InputBox>
            <InputBox>
                <img src={PasswordImg} alt=""/>
                <input type={type} value={pwd} onChange={(e)=>handleinput(e,'pwd')} placeholder="Please enter your password" autoComplete="new-password"/>
                {
                    type ==='password' && <img src={ShowImg} alt="" onClick={()=>ChangeType()}/>
                }
                {
                    type ==='text' && <img src={hideImg} alt="" onClick={()=>ChangeType()}/>
                }
                {
                    pwdShow &&  <div className="tips">* Password is required</div>
                }
                {
                    error && <div className="tips">{tips}</div>
                }

            </InputBox>
        </div>
        <BtnGroup>
            <SignBtn onClick={()=>TurnSign()}>Sign in</SignBtn>
            <LoginBtn disabled={ !user.length || !pwd.length} onClick={()=>login()}>Log in</LoginBtn>
        </BtnGroup>
        <ForgetBox onClick={()=>TurnForget()}>Forget password</ForgetBox>
    </Box>
}