import React from "react";
import styled from "styled-components";

import progress from "../assets/images/modal/progress.png";
import close from "../assets/images/iconClose.png";
import soon from "../assets/images/mobile/soon.png";

import closebtn from "../assets/images/mobile/closebtn.png";
import Btn120 from "../assets/images/btn120.png";
import {useWeb3} from "../api/contracts";
import SubNav from "./subNav";
import bg from "../assets/images/modal/bg.png";
import Mobilebg from "../assets/images/mobile/mobileModal.jpg";
import maskbg from "../assets/images/modal/mask.png";

const Middle = styled('div')`
    min-width: 2.2rem;
    text-align: center;
    @media (max-width: 1000px) {
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: center;
    }
`

const Titles = styled('div')`
    margin: 0.48rem 0 0.24rem;
    font-size: 0.48rem;
    font-family: 'orbitron';
    font-weight: bold;
    line-height: 0.56rem;
    background: linear-gradient(180deg, rgba(255, 148, 0, 0) 0%, #FFBC3C 0%, #FF9400 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
`
const BtnClose = styled('div')`
    background: url(${Btn120}) no-repeat;
    background-size: 100% 100%;
    width: 1.2rem;
    height: 0.44rem;
    margin: 0 auto;
    font-size: 0.16rem;
    font-family: "Orbitron-Medium";
    font-weight: 500;
    color: #0C0C0B;
    line-height: 0.44rem;
    cursor: pointer;
`

const PcSoon = styled('div')`
  display: block;
   @media (max-width: 1000px) {
        display: none;
    }
`;

const MobileSoon = styled('div')`
  display: none;
  .title{
    font-family: 'orbitron';
    font-weight: bold;

    background: linear-gradient(180deg, rgba(255, 148, 0, 0) 0%, #FFBC3C 0%, #FF9400 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: capitalize;
    margin-top: 48px;
    font-size: 40px;
    line-height: 48px;

    }
    .btnclose{
        background: url(${closebtn}) no-repeat;
        background-size: 100% 100%;
        width: 100px;
        height: 44px;
        margin: 0 auto;
        font-size: 14px;
        font-family: 'orbitron-regular';
        font-weight: 400;
        color: #FF9500;
        line-height: 44px;
        cursor: pointer;
        margin-top: 24px;
    }
    img{
    //width: 350px;
    //height: 324px;
    max-width: 80%;
    }
  @media (max-width: 1000px) {
        display: block;
    }
    
`;

const SubNavBox  = styled('div')`
  display: none;
   @media (max-width: 1000px) {
     display: flex;
     
     justify-content: flex-end;
     align-items: center;
     width: 100%;
     padding: 10px 20px 0 ;
     box-sizing: border-box;
    }
`

const Bgbox = styled('div')`
    background:url(${bg});
    background-size:100% 100% ;
    
     width: 100vw;
     height: 100vh;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     @media (max-width: 1000px) {
        justify-content: space-between;
        background:url(${Mobilebg}) no-repeat;
          background-size: 100% 100%;
          
    }
`

const Mask = styled('div')`
     background: url(${maskbg}) no-repeat;
     background-size: 100% 100%;
     position: fixed;
     top: 0;
     left: 0;
     width: 100vw;
     height: 100vh;
     z-index: 99;
`;


export default function Coming() {
    const {  dispatch,state } = useWeb3();
    const { comingSoon } = state;

    const handleclose = () =>{
        dispatch({type: 'SET_COMING',payload:null});
    }

    return <div>{
            comingSoon!= null  && <Mask>
                <Bgbox >
                    <SubNavBox>  <SubNav showleft={true}/></SubNavBox>
                    <Middle>
                        <PcSoon>
                            <img src={progress} alt=""/>
                            <Titles>Coming<br/> soon</Titles>
                            <BtnClose onClick={()=>handleclose()} src={close} alt="">Close</BtnClose>
                        </PcSoon>
                        <MobileSoon>
                            <img src={soon} alt=""/>
                            <div className="title">Coming soon</div>
                            <div className="btnclose" onClick={()=>handleclose()} src={close} alt="">Close</div>
                        </MobileSoon>
                    </Middle>
                </Bgbox>
            </Mask>
        }
    </div>
}
