import { ethers } from "ethers";
import abi from "../abi/NFTMarket";

let loadMain = false;
let MarketContract;

const { market } = window.GeneralConfig;

export default async function MarketConnect(state, dispatch) {

    const {web3InstanceState, MarketContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            MarketContract = new ethers.Contract(market, abi, web3Instance);
            dispatch({type: 'SET_MARKET', payload: MarketContract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MARKET_ERROR'});
        }
    };
    if (MarketContractState !== 'LOAD_MARKET') return;
    if (loadMain) return dispatch({type: 'SET_MARKET', payload: MarketContract});
    loadMain = true;
    asyncLoadMain();
}
