import styled from "styled-components";

import InputBg from "../../assets/images/login/inputbg.png";
import userImg from "../../assets/images/login/user_nor.png";
import ClearImg from "../../assets/images/login/clear_nor.png";
import CaptchaImg from  "../../assets/images/login/captcha_nor.png";
import SignBg from "../../assets/images/login/signBtn.png";
import LoginBg from "../../assets/images/login/loginBtn.png";
import btnBg from "../../assets/images/login/Btnbg.png";
import {useState} from "react";
import {useNavigate} from "react-router-dom";
import {getLogin, getVerifyCode} from "../../api/apiHttp";
import {useWeb3} from "../../api/contracts";

const Box = styled.div`
    margin-top: 0.48rem;
`

const InputBox = styled.div`
    background: url(${InputBg}) no-repeat;
    background-size: 100% 100%;
    display: flex;
    padding: 0.18rem;
    align-content: center;
    width: 4rem;
    height: 0.56rem;
    margin-bottom: 0.32rem;
    box-sizing: border-box;
    position: relative;
  .tips,.tipsSend{
    position: absolute;
    left: 0.16rem;
    bottom:-0.21rem;
    color: #EC1818;
    font-size: 0.12rem;
  }
  .tipsSend{
    color: #FF9500;
  }
    img{
        width: 0.24rem;
        height: 0.24rem;
        &:last-child{
          cursor: pointer;
        }
    }
    input{
        flex-grow: 1;
        border: 0;
        font-size: 0.14rem;
        font-family: 'orbitron-regular';
        height: 0.24rem;
        box-sizing: border-box;
        padding-top: 0.03rem;
        color: #ffffff;
        background: none;
        margin-left: 0.08rem;
      &:focus{
        outline: none;
      }
    }
  .again{
    color: #FF9500;
    opacity: 1;
  }
  span{
      color: rgba(255, 250, 242, 0.45);
      display: inline-block;
      height: 0.56rem;
      line-height: 0.56rem;
      margin-top: -0.18rem;
      border-left: 1px solid #fff;
      padding: 0 0.18rem 0 0.36rem;
      cursor: pointer;
  }
`

const BtnGroup = styled.div`
    display: flex;
    width: 4rem;
    justify-content: space-between;
    padding-top: 0.32rem;
`

const SignBtn = styled.div`
    width: 1.36rem;
    height: 0.56rem;
    background: url(${SignBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    cursor: pointer;
`
const LoginBtn = styled.button`
    width: 2.4rem;
    height: 0.56rem;
    background: url(${btnBg}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.56rem;
    text-align: center;
    font-size: 0.16rem;
    color: rgba(13, 12, 11, 1);
    border: 0;
    font-family: 'Orbitron-Medium';
    cursor: pointer;
    &:disabled{
      background: url(${LoginBg}) no-repeat;
      color: rgba(13, 12, 11, 0.45);
      background-size: 100% 100%;
    }
`

export default function Captcha(){
    const navigate = useNavigate();
    const {  dispatch } = useWeb3();

    const [ user, setUser ] = useState('');
    const [ captcha, setCaptcha ] = useState('');
    const [ again, setAgain] = useState(false);
    const [ showTime, setShowTime ] = useState(false);
    const [ time, setTime ] = useState(60);
    const [ captchaTips, setCaptchaTips ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ tips, setTips ] = useState('');

    const ClearInput = () =>{
        setUser('')
    }
    const handleinput = (e,inputType) =>{
        setError(false)
        if(inputType==='user'){
            setUser(e.target.value)
        }else{
            setCaptcha(e.target.value)
            setCaptchaTips(false)
        }
    }
    const TurnSign = () =>{
        navigate('/loginSign/3')
    }

    const GetCaptcha = () =>{
        setAgain(true)
        getVerifyCode(user).then((dataObj) =>{
            const {data} = dataObj;
            if(data.code === 200){
                setCaptchaTips(true)
            }
        })
        let timer = time;
        setShowTime(true);
        const t = setInterval(()=>{
            timer-- ;
            if(timer<= 0){
                clearInterval(t);
                setShowTime(false);
                setCaptchaTips(false)
            }
            setTime(timer);
        },1000)
    }

    const login = () =>{
        let obj = {
            account: user,
            password: null,
            verifyCode: captcha
        }
        dispatch({type: 'SET_LOADING',payload:true});
        getLogin(obj).then((dataobj)=>{
            const {data} = dataobj;
            dispatch({type: 'SET_LOADING',payload:null});
            if(data.code === 200){
                dispatch({type: 'SET_LOGIN',payload:data.data});
                navigate('/')
            }else{
                setError(true)
                setTips(data.msg);
            }
        })
    }
    return <Box>
        <div>
            <InputBox>
                <img src={userImg} alt=""/>
                <input type="text" value={user} onChange={(e)=>handleinput(e,'user')} placeholder="Phone number or Email Address" autoComplete="new-password"/>
                <img src={ClearImg} alt="" onClick={()=>ClearInput()}/>
                {/*<div className="tips">* Here is the error message</div>*/}
            </InputBox>
            <InputBox>
                <img src={CaptchaImg} alt=""/>
                <input type="text" value={captcha} onChange={(e)=>handleinput(e,'captcha')} placeholder="Please enter your password" autoComplete="new-password"/>
                {/*<span onClick={()=>GetCaptcha()}>Get</span>*/}
                {/*<div className="tips">* Here is the error message</div>*/}

                {
                    !showTime && <span onClick={()=>GetCaptcha()} className={again?' again':''}>{again?'Again':'Get'}</span>
                }
                {
                    showTime && <span className="again">{time}s</span>
                }

                {
                    captchaTips &&<div className="tipsSend"> Captcha is sent</div>
                }
                {
                    error && <div className="tips">{tips}</div>
                }
            </InputBox>
        </div>
        <BtnGroup>
            <SignBtn onClick={()=>TurnSign()}>Sign in</SignBtn>
            <LoginBtn disabled={ !user.length || !captcha.length} onClick={()=>login()}>Log in</LoginBtn>
        </BtnGroup>
    </Box>
}