import axios from "axios";

export const getLogin =async (dataObj) => {
    let data = await axios({
        method: "POST",
        url: `https://test-pc.ifighterinfinity.io/api/v1/login`,
        crossDomain: true,
        data:dataObj,
        headers: {
            'Content-Type': 'application/json '
        }
    });
    return data;
};

export const getVerifyCode =async (email) => {
    let data = await axios({
        method: "GET",
        url: `https://test-pc.ifighterinfinity.io/api/v1/send_verifycode?account=${email}`,
        crossDomain: true,
        headers: {
            'Content-Type': 'application/json '
        }
    });
    return data;
};

